nav {
  padding: 1.5rem 1rem;
}

@media (min-width: 768px) {
  nav {
    padding: 1.75rem 2rem;
  }
}

@media (min-width: 1280px) {
  nav {
    padding: 2rem 3rem;
  }
}

nav > div {
  text-align: center;
  flex-flow: column;
  align-items: center;
  gap: 2rem;
  display: flex;
}

nav > div .fields {
  width: 100%;
  max-width: 768px;
}

@media (min-width: 1024px) {
  nav > div {
    text-align: left;
    flex-flow: row;
    justify-content: center;
    gap: 4rem;
  }

  nav > div > div:first-child {
    width: 260px;
  }
}

nav p {
  color: #fffc;
  margin-bottom: 1rem;
}

nav form {
  flex-flow: wrap;
  place-items: center;
  display: flex;
}

nav label[for="ano"] {
  max-width: 100px;
  flex: 100px;
}

nav label[for="semestre"] {
  max-width: 150px;
  flex: 150px;
}

nav label[for="busca"] {
  flex: 2 320px;
}

.categories {
  flex-flow: column;
  gap: 3rem;
  padding: 1.5rem 1rem 3rem;
  display: flex;
}

@media (min-width: 768px) {
  .categories {
    padding: 1.75rem 2rem 3.5rem;
  }
}

@media (min-width: 1280px) {
  .categories {
    padding: 2rem 0 4rem;
  }
}

.categories .category {
  width: 100%;
  flex-flow: column;
  gap: 1rem;
  display: flex;
}

.categories .category header {
  flex: 1;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.categories .cards {
  width: 100%;
  grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
  gap: 1rem;
  display: grid;
}

@media (min-width: 768px) {
  .categories .cards {
    grid-template-columns: repeat(auto-fill, minmax(380px, 1fr));
  }
}

.categories .cards.large .card {
  flex-direction: row;
  align-items: start;
  padding: 1.25rem 1.5rem 1.125rem;
}

.categories .cards.large .card .details {
  gap: .25rem;
}

.categories .cards:not(.large) .card {
  align-items: center;
  padding: 1.5rem 1.125rem 1.25rem;
}

.categories .cards:not(.large) .card .details {
  max-width: 100%;
  align-items: center;
  gap: .5rem;
}

.categories .cards:not(.large) .card .badges.statuses {
  align-items: center;
}

.categories .cards .card {
  min-width: 0;
  gap: 2rem 1.5rem;
}

.categories .cards .card picture {
  width: 5.75rem;
  height: 5.75rem;
  border-radius: var(--rounded-md);
  box-shadow: var(--shadow-card-rest);
  background: #fff;
  border: 1px solid #0000000f;
  align-self: center;
  display: block;
  overflow: hidden;
}

.categories .cards .card picture img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: var(--rounded-md);
  display: block;
}

.categories .cards .card picture:empty {
  opacity: .92;
  background: url("../images/empty-project.653dbbcf.svg") 0 0 / cover;
}

.categories .cards .card .details {
  min-width: 0;
  flex-flow: column;
  flex: 1;
  display: flex;
}

.categories .cards .card .details h3, .categories .cards .card .details .title {
  color: var(--text-primary);
  font-size: var(--font-size-body-large);
  line-height: var(--leading-body-large);
  height: 1.25rem;
  max-width: 100%;
}

.categories .cards .card .details p, .categories .cards .card .details .description {
  color: var(--text-secondary);
  font-size: var(--font-size-label);
  line-height: var(--leading-label);
}

.categories .cards .card .details .badges.statuses {
  margin-top: .375rem;
}

.categories .cards:not(.large) {
  grid-template-columns: repeat(auto-fill, minmax(140px, 1fr));
}

@media (min-width: 768px) {
  .categories .cards:not(.large) {
    grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
  }
}

@media (min-width: 1280px) {
  .categories .cards:not(.large) {
    grid-template-columns: repeat(auto-fill, minmax(180px, 1fr));
  }
}

